import {
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  EventEmitter,
  OnDestroy,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { Subject } from 'rxjs';
import {NxPopoverContentDirective} from './popover-content';
import {ENTER, SPACE} from '@angular/cdk/keycodes';

@Component({
  selector: 'nx-popover',
  templateUrl: './popover.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./popover.component.scss'],
  exportAs: 'nxPopover',
})
export class NxPopoverComponent implements OnDestroy {
  /** @docs-private */
  @ViewChild(TemplateRef, { static: false })
  templateRef: TemplateRef<any>;

  /** Content that will be rendered lazily. */
  @ContentChild(NxPopoverContentDirective, {static: false}) _lazyContent: NxPopoverContentDirective;

  /** Event emitted when the popover is closed. */
  @Output('nxClosed')
  closed = new EventEmitter<void>();

  /** @docs-private */
  closeButtonClick = new Subject<void>();

  /** @docs-private */
  id: string;

  /** @docs-private */
  direction;

  /** @docs-private */
  showCloseButton: boolean = false;

  /** @docs-private */
  arrowStyle = {};

  constructor() {
  }

  ngOnDestroy() {
    this.closed.complete();
  }

  // emit to notify the popover trigger directive that the close button was clicked
  /** @docs-private */
  emitCloseButtonClick() {
    this.closeButtonClick.next();
  }

  /** @docs-private */
  _onCloseKeyup($event) {
    if ($event && ($event.keyCode === ENTER || $event.keyCode === SPACE)) {
      this.emitCloseButtonClick();
    }
    $event.preventDefault();
  }

  /** @docs-private */
  emitClosedEvent() {
    this.closed.emit();
  }

  /** @docs-private */
  get classList(): string[] {
    if (this.direction) {
      return [`nx-popover--${this.direction}`];
    }
  }

  /** Prevent the popover from closing when the user clicks on the popover content. */
  _onClick(event) {
    event.stopPropagation();
  }
}
